	module.exports = {"data":{"markdownRemark":{"html":"<p>やあ （´・ω・｀) ようこそ、バーボンハウスへ。 このテキーラはサービスだから、まず飲んで落ち着いて欲しい。 うん、「また」なんだ。済まない。 仏の顔もって言うしね、謝って許してもらおうとも思っていない。</p>\n<h2>時封城v2延期について</h2>\n<p>夢終劇において在籍絵師スタッフの進行等の都合上、急遽自分が一部の絵の作業に参加することになりました。よって時封城v2製作の時間が取れそうに無いので今回止むを得ず時封城v2を延期とします。C71の出し物は夢終劇とLeila Prismriverの2作品のみとなります。 なお、時封城v2のリリース予定イベントは未定です。(C72だと時間経ちすぎだし……)2度も延期となってしまったことをお詫び申し上げます。</p>\n<h2>夢終劇体験版C1.02リリース</h2>\n<p><a href=\"/!/thC/\">東方夢終劇 体験版C1.02</a>をリリースします。スペルプラクティスと新システム搭載を中心に他細かい機能もいくつか搭載しました。</p>\n<h2>その他のリリース</h2>\n<p>東方弾幕風フレームワークライブラリ『<a href=\"/tag/flan\">Framework of LunAtic Notation</a>』のバージョン0.2cをリリースしました。今回はマイナーな機能追加、バグ修正のみです。 また、<a href=\"/emeditor-danmakufu\">EmEditor用東方弾幕風構文ファイル</a>も同時に更新、一部バグ修正を施しました。</p>","frontmatter":{"cover":null,"date":"2006-11-08T00:00:00+09:00","strDate":"2006/11/7","title":"リリースとか延期とか諸々告知"}}},"pathContext":{"path":"/ja/blog/2006/11/08-リリースとか延期とか諸々告知/","langKey":"ja"}}

